// DashboardLayout.js
import React, { useState } from 'react';
import {
  Box,
  Grid,
  Container,
  IconButton,
  Drawer,
  List,
  ListItemButton,
  ListItemText,
  Typography,
  AppBar,
  Toolbar
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import FiltersPanel from './FiltersPanel';
import SummaryCards from './SummaryCards';
import ChartsPanel from './ChartsPanel';
import ItemsTable from './ItemsTable';
import { useDashboard } from '../context/DashboardContext';

export default function DashboardLayout({
  summary,
  chartsData,
  tableData,
  loading,
  onFilterChange,
  onPageChange,
  onPageSizeChange,
  onSortChange
}) {
  const { filters, setFilters } = useDashboard();
  const [drawerOpen, setDrawerOpen] = useState(false);

  const menuItems = [
    { label: 'Dashboard', onClick: () => console.log('Dashboard clicked') },
    { label: 'Reports', onClick: () => console.log('Reports clicked') },
    { label: 'Settings', onClick: () => console.log('Settings clicked') },
  ];

  const sidebar = (
    <Box sx={{ width: 240, bgcolor: '#1e1e2f', color: 'white', height: '100%' }}>
      <Typography variant="h6" sx={{ p: 2 }}>Menu</Typography>
      <List>
        {menuItems.map((item, idx) => (
          <ListItemButton key={idx} onClick={item.onClick}>
            <ListItemText primary={item.label} />
          </ListItemButton>
        ))}
      </List>
    </Box>
  );

  return (
    <Box sx={{ display: 'flex', minHeight: '100vh', bgcolor: '#f7f8fa' }}>
      {/* AppBar for mobile */}
      <AppBar position="fixed" sx={{ display: { md: 'none' } }}>
        <Toolbar>
          <IconButton edge="start" color="inherit" onClick={() => setDrawerOpen(true)}>
            <MenuIcon />
          </IconButton>
          <Typography variant="h6">Dashboard</Typography>
        </Toolbar>
      </AppBar>

      {/* Sidebar for desktop */}
      <Box
        sx={{
          display: { xs: 'none', md: 'block' },
          width: 240,
          flexShrink: 0,
          position: 'fixed',
          top: 0,
          bottom: 0,
        }}
      >
        {sidebar}
      </Box>

      {/* Drawer for mobile */}
      <Drawer
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
        sx={{ display: { xs: 'block', md: 'none' } }}
      >
        {sidebar}
      </Drawer>

      {/* Main Content */}
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          ml: { md: '240px' },
          pt: { xs: 8, md: 2 },
          px: 2,
        }}
      >
        <Container maxWidth="xl" sx={{ mt: 2, mb: 4 }}>
          {/* Summary Cards */}
          <SummaryCards metrics={summary} />

          {/* Charts */}
          <Box sx={{ mt: 4, mb: 4 }}>
            <ChartsPanel items={chartsData || []} />
          </Box>

          <Grid container spacing={2} sx={{ mt: 2 }}>
            {/* Filters Panel */}
            <Grid item xs={12} md={3}>
              <FiltersPanel onFilterChange={onFilterChange} />
            </Grid>

            {/* Items Table */}
            <Grid item xs={12} md={9}>
              <ItemsTable
                items={tableData.items}
                total={tableData.total}
                page={tableData.page}
                pageSize={tableData.pageSize}
                loading={loading}
                onPageChange={onPageChange}
                onPageSizeChange={onPageSizeChange}
                onSortChange={onSortChange}
              />
            </Grid>
          </Grid>
        </Container>
      </Box>
    </Box>
  );
}
